import search_bar from '@/components/common/search.bar'

import keywords_conf from '@/assets/configs/web/keywords.json'

export default {
  name: "header.menu",
  components: {
    search_bar
  }
  ,
  data() {
    return {
      keywords_conf: keywords_conf
    }
  },
  created() {
    console.log('keywords_conf:', this.keywords_conf);
  },
  computed: {
    recommend_keywords1: function () {
      return this.keywords_conf.slice(0, 10)
    },
    recommend_keywords2: function () {
      return this.keywords_conf.slice(10, 20)
    },
    recommend_keywords3: function () {
      return this.keywords_conf.slice(20, 30)
    }
  }
}
