export default {
  name: "header.usercenter",
  components: {},
  created() {
    this.listener_login = this.$hub.on('event:login', (data) => {
      this.$forceUpdate();
    });
    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });
  },
  destroyed() {
    this.listener_loginout.off();
    this.listener_login.off();
  },
  data() {
    return {}
  },
  methods: {
    loginout: function () {
      this.$store.commit('loginout');
      this.$forceUpdate();
    },
    on_user_center_click: function () {
      if (!this.$store.state.hasLogin) {
        this.$app.show_login_dialog();
      }
    },
  }
}
